import { observer } from 'mobx-react-lite';
import React, { useState } from 'react';
import { useDrag } from 'react-dnd';
import { useStore } from '../../store'; // 
import '../../styles/ReadmeElement.css';

const ReadmeElement = ({ id, type, content, onRemove, onEdit }) => {
  const { elementStore } = useStore(); // store
  const [{ isDragging }, drag] = useDrag({
    type: 'EXISTING_ELEMENT',
    item: { id, type, content },
    collect: (monitor) => ({
      isDragging: monitor.isDragging(),
    }),
  });

  // 
  const isContentArea = type === '';
  const isEditing = elementStore.editingContentAreaId === id;

  const handleEdit = () => {
    if (isContentArea) {
      // 
      elementStore.setEditingContentArea(id);
    } else {
      // 
      setLocalIsEditing(true);
    }
  };

  const handleSave = (event) => {
    // handleEdit
    event.stopPropagation();

    if (isContentArea) {
      // 
      if (typeof onEdit === 'function') {
        onEdit(id, editContent);
      } else {
        console.error('onEdit is not a function', onEdit);
      }
      elementStore.cancelEditingContentArea();
    } else {
      // 
      if (typeof onEdit === 'function') {
        onEdit(id, editContent);
        setLocalIsEditing(false);
      } else {
        console.error('onEdit is not a function', onEdit);
      }
    }
  };

  const [localIsEditing, setLocalIsEditing] = useState(false);
  const [editContent, setEditContent] = useState(content);

  // 
  const getElementStyle = () => {
    switch (type) {
      case '':
        return 'readme-title';
      case '':
        return 'readme-paragraph';
      case '':
        return 'readme-list';
      case '':
        return 'readme-badge';
      case '':
        return 'readme-code';
      case '':
        return 'readme-content-area';
      default:
        return '';
    }
  };

  // 
  const renderEditComponent = () => {
    switch (type) {
      case '':
      case '':
      case '':
      case '':
        return (
          <input
            type="text"
            value={editContent}
            onChange={(e) => setEditContent(e.target.value)}
          />
        );
      case '':
        return (
          <textarea
            value={editContent}
            onChange={(e) => setEditContent(e.target.value)}
            rows={4}
          />
        );
      case '':
        return (
          <div className="content-area-edit">
            <p></p>
            <input
              type="text"
              placeholder="..."
              value={editContent}
              onChange={(e) => setEditContent(e.target.value)}
            />
          </div>
        );
      default:
        return null;
    }
  };

  return (
    <div
      ref={drag}
      className={`readme-element ${getElementStyle()} ${isDragging ? 'dragging' : ''}`}
      style={{ opacity: isDragging ? 0.5 : 1 }}
    >
      {isContentArea ? (
        // 
        <> 
          <div 
            className={`content-area-container ${isEditing ? 'content-area-editing' : ''}`} 
            onClick={handleEdit}
          >
            {isEditing ? (
              <div className="edit-buttons content-area-save-button">
                <button onClick={(e) => handleSave(e)}></button>
              </div>
            ) : (
              <div className="content-area-label">
                {content || ''}
              </div>
            )}
          </div>
          <button className="remove-button" onClick={() => onRemove(id)}></button>
        </>
      ) : (
        // 
        localIsEditing ? (
          <div className="edit-container">
            {renderEditComponent()}
            <div className="edit-buttons">
              <button onClick={handleSave}></button>
              <button onClick={() => setLocalIsEditing(false)}></button>
            </div>
          </div>
        ) : (
          <> 
            {type === '' && <h2 onClick={handleEdit}>{content}</h2>}
            {type === '' && <p onClick={handleEdit}>{content}</p>}
            {type === '' && <ul><li onClick={handleEdit}>{content}</li></ul>}
            {type === '' && (
              <div className="badge-container" onClick={handleEdit}>
                <img src={content} alt="" />
              </div>
            )}
            {type === '' && <pre onClick={handleEdit}><code>{content}</code></pre>}
            <button className="remove-button" onClick={() => onRemove(id)}></button>
          </>
        )
      )}
    </div>
  );
};

export default observer(ReadmeElement);